"use client"

import { useState, useEffect } from "react"
import { Header } from "@/components/layout/header"
import { UserEditForm } from "@/components/users/edit/user-edit-form"
import { mockUser } from "@/lib/mock-users"
import { UserProfileSkeleton } from "@/components/ui/skeletons"

export default function UserEditPage() {
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    // Симуляция загрузки данных
    const timer = setTimeout(() => {
      setIsLoading(false)
    }, 1000)

    return () => clearTimeout(timer)
  }, [])

  return (
    <div className="min-h-screen bg-background">
      <Header />
      {isLoading ? <UserProfileSkeleton /> : <UserEditForm user={mockUser} />}
    </div>
  )
}
